import Vue from "vue"
import Vuex from "vuex"
Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		tit: null,
		page:null
	},
	getters: {

	},
	mutations: {
		setCount(state, data) {
			state.tit = data
		},
		setPage(state, data) {
			state.page = data
		}
	},
	actions: {
		setname(context, data) {
			context.commit('setCount', data)
		},
		settab(context, data){
			context.commit('setPage', data)
		}
	}
})


