<template>
	<div class="tab">
		<ul>
			<li v-for="arr,index in arrs " :class="{'babbar-on':page==index}" @click="changeTab(index)">{{arr}}</li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: "tab",
		data() {
			return {
				arrs: ["微信", "通讯录", "发现", "我"],
				page: 0
			}
		},
		methods: {
			changeTab(page) {

				this.page = page
				this.$store.dispatch('settab', this.page);

			}
		}
	}
</script>

<style scoped>
	.tab {
		position: fixed;
		bottom: 0;
		width: 100%;
		background: black;
	}
	
	.tab li {
		float: left;
		font-size: .3rem;
		border: 1px solid #FF0000;
		width: 25%;
		box-sizing: border-box;
		color: white;
		text-align: center;
	}
	
	.babbar-on {
		background: #42B983;
	}
</style>


