<template>
	<div class="titles">
		<div class="tit">
			<i v-if="backb" @click="back">返回</i> {{tit}}
		</div>
	<i v-scrollTop v-if="top"  class="top">置顶</i>
		
	</div>
</template>

<script>
	export default {
		name: "titles",
		props: ["tit"],
		data() {
			return {
				backb:null,
				scroll:null,
				top:false
			}
		},
		created() {
			//			this.tits()
			this.backbs()
		},
		methods: {
			back() {
				this.$router.go(-1)
			},
			//			tits(){
			//				this.$store.dispatch('setname',"首页");
			//			}
			backbs() {
				if(this.$route.name=="detail"){
					this.backb=true
				}else{
					this.backb=false
					
				}
			},
			scro(){
				this.scroll=document.body.scrollTop
				if(this.scroll>=500){
					this.top=true
				}else{
					this.top=false
					
				}
			}
		},
		computed: {
			//			tit(){
			//				return this.$store.state.tit
			//			}
		},
		update(){

			console.log(this)
			
		}
		,
		mounted() {
       window.addEventListener('scroll', this.scro, true)


		},
		directives: {
			scrollTop: {
				bind: function(el) {
					el.addEventListener("click", function() {
						console.log("置顶")
						var timer = setInterval(function() {
							document.body.scrollTop = document.body.scrollTop - 50;
							if(document.body.scrollTop == 0) {
								clearInterval(timer)
							}
						}, 1)
					})
				}
			}
		}
	}
</script>

<style scoped>
	.tit {
		background: red;
		font-size: .5rem;
		position: fixed;
		top: 0;
		width: 100%;
		text-align: center;
		/*position: relative;*/

		
	}
	.titles{
height: 0.66rem;
		
	}

	
	.tit i {
		position: absolute;
		left: .2rem;
		font-size: .2rem;
		top: .15rem;
	}
	.top{
		width: 1rem;
		height: 1rem;
		background: #000000;
		border-radius: 50%;
		position: fixed;
		bottom: 1rem;
		right: .5rem;
		font-size: .3rem;
		text-align: center;
		line-height:1rem ;
		
	}
</style>


