<template>
	<div class="detail">
		<titles :tit="detail"></titles>
		<swiper></swiper>
		<detailbo></detailbo>
	</div>
</template>

<script>
	import  titles from "./common/title"
	import  swiper from "./Details/swiper"
	import detailbo from "./Details/detailbody.vue"
	
	export default {
		name: "detail",
		data() {
			return {
				detail:"详情页"
			}
		},
		components:{
			titles,
			swiper,
			detailbo
		}
		,
		computed: {
			detailId() {
				return this.$route.params.id
			}
		}
	}
</script>

<style scoped="">
	.detail {}
	
	.detail div {
		width: 100%;


	}
</style>


