<template>
	<div class="list">
		<a v-for="n in list" :href="'#/detail/'+n.id">
			<img :src="list ? n.author.avatar_url : './logo.png'" />
			<div class="list_tit">
				<p>{{n.title}} </p>
			</div>
		</a>


	</div>
</template>

<script>
	export default {
		name: "list",
		data() {
			return {
				list: null
			}
		},

		methods: {
			listJson() {
				this.$ajax.get('https://cnodejs.org/api/v1//topics')
					.then((json) => {
						console.log(json)

						this.list = json.data.data
						console.log(this.list)

					}).catch((err) => {
						console.log(err)
					})
			}
		},
		mounted() {
			this.listJson()
		},
		watch: {
			'$store.state.page' (newFlag, oldFlag) {
				//				this.listJson()
				if(newFlag == 1) {
					this.list = [{
						author: {
							avatar_url: "data:image/png;base64,"
						}
					}]
				} else if(newFlag == 3) {
					this.list = [{
						author: {
							avatar_url: "data:image/png;base64,"
						}
					}]
				} else {
					this.listJson()
				}
			}
		}
	}
</script>

<style scoped="">
	.list {

		padding-bottom: .24rem;
	}
	
	.list a {
		width: 100%;
		display: inline-block;
		color: black;
	}
	
	.list img {
		width: 1rem;
		height: 1rem;
		float: left;
	}
	
	.list .list_tit p {
		float: left;
		font-size: .3rem;
	}
	
	.geng {}
</style>


